<template>
  <div
    class="layout-side-nav">
    <xdh-menu-toggle
      class="menus"
      :data="menus"
      :collapse="collapse"
      @select="handleSelectMenu"></xdh-menu-toggle>
    <div
      class="foot-btn"
      @click="handletToggle">
      <div
        :class="{'el-icon-d-arrow-left':true,'expand':collapse}"
        type="text">
      </div>
    </div>
  </div>
</template>
<script>
  import XdhMenuToggle from '@/widgets/xdh-menu-toggle'
  import {traverse} from '@/utils/util';

  export default {
    components: {
      XdhMenuToggle
    },
    data() {
      return {
        collapse: false,
        menus: [{
          id: 1,
          text: '处理中心',
          icon: 'el-icon-goods',
          path: '/sys/dict'
        }, {
          id: 2,
          text: '我的工作台',
          icon: 'el-icon-setting',
          children: [{
            id: 21,
            text: '选项一',
            path: '/sys/work'
          }]
        }]
      }
    },
    methods: {
      /**
       * 点击菜单获取选中的菜单id
       * @param id 选中的菜单id
       */
      handleSelectMenu(id) {
        let item = this.findItemById(this.menus, parseInt(id))
        if (item.path) this.$router.push(item.path)
      },
      /**
       * 根据菜单id查找出数据项对象
       * @param data 菜单
       * @param id 要查找菜单项的id
       */
      findItemById(data, id) {
        return traverse(data, true, function (item) {
          return item.id === id
        })
      },
      /**
       * 菜单收放事件
       */
      handletToggle() {
        this.collapse = !this.collapse
        this.$emit('on-change-width', this.collapse ? '66px' : '230px')
      }

    }
  }
</script>
<style lang="scss" scoped>
  .layout-side-nav {
    height: 100%;
    border: 1px solid #eaeaf4;
    display: flex;
    flex-direction: column;
    .menus {
      flex: 1;
      overflow-y: auto;
    }
    .foot-btn {
      height: 40px;
      line-height: 40px;
      text-align: center;
      font-size: 20px;
      color: #CCCCCC;
      background: #eaeaf4;
    }
    .el-icon-d-arrow-left {
      -webkit-transition: transform 0.3s;
      -moz-transition: transform 0.3s;
      -ms-transition: transform 0.3s;
      -o-transition: transform 0.3s;
      transition: transform 0.3s;
      &.expand {
        -webkit-transition: transform 0.3s;
        -moz-transition: transform 0.3s;
        -ms-transition: transform 0.3s;
        -o-transition: transform 0.3s;
        transition: transform 0.3s;
        -webkit-transform: rotate(180deg);
        -moz-transform: rotate(180deg);
        -ms-transform: rotate(180deg);
        -o-transform: rotate(180deg);
        transform: rotate(180deg);
      }
    }

  }
</style>
