/**
 * 路由初始化
 * @module router/index
 */
import Vue from 'vue'
import Router from 'vue-router'
import routes from './routes'

/**
 * 注册导航tabs store模块
 */
import store from '../store/index'
import creadeStoreModule from '@/widgets/xdh-nav-tabs/store'

const TABS_STORE = 'layoutTabs'
if (!store.state[TABS_STORE]) {
  store.registerModule(TABS_STORE, creadeStoreModule())
}

const router = new Router(routes)

Vue.use(Router)

router.afterEach((to) => {
  if (to.meta.tab) {
    let flag
    to.path === '/' ? flag = false : flag = true
    store.commit(`${TABS_STORE}/push`, {
      tab: {
        label: to.name || '',
        name: to.path || '',
        closable: flag
      }
    })
    console.log(store, 'store2')
  } else {
    store.commit(`${TABS_STORE}/clear`)
  }
})

export default router
