<template>
  <xdh-grid :data.sync="imgList" direction="row" justify="flex-start" wrap="wrap" sortable>
    <div class="box-wrapper" slot-scope="scope">
      <div class="box">
        <img :src="scope.item.src" alt=""/>
      </div>
    </div>
  </xdh-grid>
</template>
<style lang="scss" scoped>
  .box-wrapper{
    padding: 10px;
    background: #ccc;
  }
  .box {
    display: table-cell;
    background: #ffffff;
    width: 150px;
    height: 150px;
    text-align: center;
    vertical-align: middle;
  }

  img {
    width: 120px;
    max-width: 100%;
    max-height: 100%;
    display: block;
    margin: auto;
  }

  .xdh-grid__item.sortable-ghost .box {
    background: orange;
    opacity: 0.3;
  }
</style>
<script>
  import XdhGrid from '@/widgets/xdh-grid'

  export default {
    components: {
      XdhGrid
    },
    data() {
      return {
        imgList: []
      }
    },
    methods: {
      getImgs() {
        for (let i = 1; i <= 30; i++) {
          this.imgList.push({
            src: `/img/${i}.jpg`
          })
        }
      }
    },
    created() {
      this.getImgs()
      console.log(this.imgList, 'imgList')
    }
  }
</script>
